<template>
    <div id="app">

        <div class="header">
            <div class="center">

                <div class="logo__img"></div>

                <span class="header__text" v-for="(value, key) in data.header" v-html="value"></span>
            </div>
        </div>

        <div class="personal">
            <div class="center">
                <p class="personal__text" v-html="data.personal"></p>

                <div class="about">
                    <img src="./assets/images/rabbit.png" alt="" class="about__img">
                    <img src="./assets/images/rabbit__small.png" alt="" class="about__img about__img_small">

                    <p class="about__text" v-html="data.about"></p>
                </div>
            </div>
        </div>

        <div class="video" :class="[{video__open: isShowVideo}, {video_height: isShow[2] || isShow[1]}]">
            <div class="video__head" v-if="!isShowVideo">
                <div class="center">
                    <h6 class="video__caption" v-html="data.videoCaption"></h6>
                </div>
            </div>

            <div class="center">
                <div class="wrap">

                    <p class="video__caption video__caption_small" v-html="data.videoSmallCaption" v-if="isShowVideo"></p>
                    <div class="video__item video__item_0" @click="showVideo(0)" :class="{video__main: isShow[0]}">
                        <div class="main-video-wrap">
                            <transition name="fade">
                                <iframe class="video__iframe" src="https://www.youtube.com/embed/LpBtam5t_OY" frameborder="0" allow="autoplay; encrypted-media" allowscriptaccess="always" allowfullscreen v-show="isShow[0]"></iframe>
                            </transition>
                            <img src="./assets/images/1.png" alt="" class="video__preview" v-if="!isShow[0]">

                            <div class="video__info">
                                <p class="video__name" v-html="data.videos[0].name"></p>
                                <p class="video__descr" v-html="data.videos[0].descr"></p>
                                <p class="video__duration" v-html="data.videos[0].duration"></p>
                            </div>
                        </div>


                    </div>
                    <div class="video__item video__item_1" @click="showVideo(1)" :class="{video__main: isShow[1]}">
                        <div class="main-video-wrap">
                            <transition name="fade">
                                <iframe class="video__iframe" src="https://www.youtube.com/embed/gTKBvH4i3n4" frameborder="0" allow="autoplay; encrypted-media" allowfullscreen allowscriptaccess="always" v-show="isShow[1]"></iframe>
                            </transition>
                            <img src="./assets/images/2.png" alt="" class="video__preview" v-if="!isShow[1]">

                            <div class="video__info">
                                <p class="video__name" v-html="data.videos[1].name"></p>
                                <p class="video__descr" v-html="data.videos[1].descr"></p>
                                <p class="video__duration" v-html="data.videos[1].duration"></p>
                            </div>
                        </div>
                    </div>
                    <div class="video__item video__item_2" @touchend, @click="showVideo(2, $event)" :class="{video__main: isShow[2]}">
                        <div class="main-video-wrap">
                            <transition name="fade">
                                <iframe class="video__iframe" src="https://www.youtube.com/embed/hBwNSLLNUeA" frameborder="0" allow="autoplay; encrypted-media" allowscriptaccess="always" allowfullscreen v-show="isShow[2]"></iframe>
                            </transition>
                            <img src="./assets/images/3.png" alt="" class="video__preview" v-if="!isShow[2]">

                            <div class="video__info">
                                <p class="video__name" v-html="data.videos[2].name"></p>
                                <p class="video__descr" v-html="data.videos[2].descr"></p>
                                <p class="video__duration" v-html="data.videos[2].duration"></p>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
        </div>

        <div class="info" :class="[{info_1: isShow[1]}, {info_2: isShow[2]}]">
            <div class="center">
                <div class="left">
                    <h6 class="info__caption" v-html="data.info.caption"></h6>

                    <img src="./assets/images/pack.png" alt="" class="info__img">

                    <p class="info__text" v-for="(value, key) in data.info.leftText" v-html="value"></p>
                </div>
                <div class="right">
                    <p class="info__text" v-for="(value, key) in data.info.rightText" v-html="value"></p>
                </div>
            </div>
        </div>

        <div class="footer">
            <div class="center">
                <img src="./assets/images/teva.png" alt="" class="footer__img">
                <p class="footer__text" v-html="data.footer.text"></p>
            </div>
        </div>

    </div>
</template>

<script>
    import data from "./data/data.js"
    import "gsap"


    export default {
        name: 'app',

        data(){
            return {
                data: data,

                isShow: [false, false, false],
                isShowVideo: false
            }
        },

        methods: {
            showVideo(key, event){
                var iframe = document.querySelector( '.video__main iframe');
                console.log(event.type);
                if ( iframe ) {
                    var iframeSrc = iframe.src;
                    iframe.src = iframeSrc;
                }
                let elem = document.getElementsByClassName('video__item')[key];
                console.log(elem.getBoundingClientRect().top + pageYOffset);

                this.isShow = [false, false, false];
                this.isShowVideo = true;
                this.$set(this.isShow, key, !this.isShow[key]);



                this.$nextTick(function(){
                    window.scrollTo(0, elem.getBoundingClientRect().top + pageYOffset);
                })
            }
        },

        mounted(){
            this.$nextTick(function(){
                if (document.documentMode || /Edge/.test(navigator.userAgent)) {
                    document.body.classList.add('ie')
                }
                if (navigator.userAgent.indexOf("Firefox") > 0) {
                    document.body.classList.add('firefox')
                }
                if ( /^((?!chrome|android).)*safari/i.test(navigator.userAgent)) {
                    document.body.classList.add('safari')
                }
            })
        },

        components: {

        }
    }
</script>

<style lang="sass">
    @import "style/_fonts"
    @import "style/_variables"

    *
        margin: 0
        padding: 0
        border: 0
        -webkit-box-sizing: border-box
        -moz-box-sizing: border-box
        box-sizing: border-box
    sup, sub
        vertical-align: baseline
        position: relative
        top: -0.4em

    sub
        top: 0.4em

    .header
        width: 100%
        height: 80px
        background-color: #d23764
        display: flex
        flex-wrap: wrap
        align-items: center
        justify-content: left
        .center
            display: flex
            align-items: center

    .center
        width: 90%
        max-width: 1360px
        height: auto
        margin: 0 auto
        padding: 0 10px

    .logo__img
        width: 300px
        height: 50px
        background-image: url(./assets/images/logo.svg)
        -webkit-background-size: 100% 100%
        background-size: 100% 100%
        background-repeat: no-repeat

    .header__text
        +font(600, normal)
        font-size: 20px
        line-height: 25px
        color: #fff
        margin-left: 2vw

    .personal
        min-height: 340px
        background-color: #fff
        .center
            display: flex
            align-items: center
            justify-content: space-between

    .personal__text
        max-width: 880px
        font-size: 32px
        color: #414141
        +font(normal, normal)
        span
            +font(600, normal)

    .about
        position: relative
    .about__img_small
        display: none

    .about__text
        position: absolute
        top: 74px
        left: 46px

        max-width: 190px
        text-align: center

    .about__text
        +font(600, normal)
        font-size: 16px
        color: #414141
        span
            color: #d23764

    .video
        min-height: 580px
        background-color: #57d9cc

    .video__item:not(.video__main)
        .video__info
            display: flex
            flex-wrap: wrap
            align-items: flex-start
            padding-bottom: 10px
        .video__name
            margin-top: 0

    .video__head
        width: 100%
        height: 90px
        border-bottom: 1px solid grey

    .video__caption
        +font(normal, normal)
        font-size: 34px
        color: #000
        padding-top: 40px

    .video
        .wrap
            display: flex
            justify-content: flex-start
            flex-wrap: wrap
            &:after
                content: "";
                clear: both;
                display: table;


    .video__item
        width: 320px
        cursor: pointer
        padding-top: 30px
        margin-right: 20px
    .video__open
        .video__preview
            width: 42%
            height: auto
            -o-object-fit: contain
            object-fit: contain
            object-position: 0 0
        .video__item
            margin-right: 0
    .video__preview
        width: 320px
    .video__name
        +font(600, normal)
        font-size: 24px
        color: #000
        margin-top: 30px

    .video__descr
        +font(normal, normal)
        font-size: 24px
        color: #000
    .video__duration
        +font(600, normal)
        font-size: 24px
        color: #000
        padding-left: 40px
        margin-top: 20px
        position: relative
        &:before
            content: url('./assets/images/play.svg')
            width: 30px;
            height: 30px;
            position: absolute
            left: 0
            top: 0

    .video__main
        order: -1
        width: 49%
        height: 900px
        border-right: 1px solid grey
        /*transition: 1s ease-in width */
        cursor: auto

        iframe
            width: 100%
            height: 50%
            object-fit: contain;
            margin: 0 auto
        .video__info
            width: 100%
            height: 50%
            background-color: #fff
            padding: 20px
            margin-top: -6px

            background-image: url(./assets/images/rabbit_1.png)
            background-repeat: no-repeat
            background-position: 200% bottom
            -webkit-background-size: 80% 70%
            background-size: 80% 70%

    .ie
        .video__open
            .video__main.video__item_2
                top: -249px
        .info_2
            transform: translateY(-260px)
    .video__open
        display: block
        &:after
            content: ""
            clear: both
            display: table

        .wrap
            display: block
        .video__main
            float: left
            width: 49%
        .video__main.video__item_1
            position: relative
            top: -78px
        .video__main.video__item_2
            position: relative
            top: -323px
        .video__item_2
            .video__name
        .video__item:not(.video__main)
            width: 51%
            height: 400px
            padding-left: 20px
            float: right
            display: flex
            height: auto
            .video__info
                float: left
                margin-left: 20px
                width: 58%
                border-bottom: 1px solid black
            .video__preview
                float: left

    .video_height
        height: 1000px
        max-height: 900px
    .video__caption_small
        font-size: 30px
        text-transform: uppercase
        width: 50%
        float: right
    .video__item_2:not(.video__main)
        .video__name


    .info
        padding-bottom: 100px
        border-bottom: 4px solid #dedede
        .center
            display: flex
            justify-content: space-between
            flex-wrap: wrap
            padding-top: 0
            .left, .right
                width: 48%

    .info__caption
        +font(600, normal)
        font-size: 42px
        color: #c51162
        margin-top: 65px
        margin-bottom: 130px
    .info__img
        width: 70%
        height: auto
        margin: 0 auto 100px auto
        display: block
    .info__text
        +font(300, normal)
        font-size: 16px
        color: #414141
        margin-top: 30px
        span
            +font(500, normal)

    .right
        .info__text:first-child
            margin-top: 260px

    .footer
        .center
            display: flex
            height: 100px
            padding: 0 10px
            justify-content: space-between
            align-items: center
            flex-wrap: wrap

    .footer__text
        +font(300, normal)
        font-size: 26px
        color: #414141

    .info_1
        transform: translateY(-100px)
    .info_2
        transform: translateY(-340px)

    .main-video-wrap
        width: 100%
        height: calc(100% - 35px)
    .video__main
        .main-video-wrap
            float: left
            width: 96%
            flex-wrap: wrap
            box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.5)
            align-items: flex-start
    .video__open
        .main-video-wrap
            display: flex


    .firefox
        .video__open
            .video__item_2.video__main
                top: -247px
        .info_2
            transform: translateY(-280px)


    .safari
        .video__main
            .main-video-wrap
                display: block;
                position: relative;
                iframe
                    position: absolute;
                    top: 0;
                .video__info
                    position: absolute;
                    bottom: 0;



    @import "style/_media"
</style>
